.inputWrap input {
  padding: 0;
  margin: 0;
  text-transform: uppercase;
}
.inputWrap input[type='text'] {
  box-sizing: border-box;
}
.inputWrap input::placeholder {
  text-transform: none;
}
.inputWrap {
  width: 200px;
  margin-right: 20px;
  margin-bottom: 5px;
}
.inputWrap label {
  font-weight: bold;
}
.inputWrap select {
  padding: 0px;
  margin: 0px;
}

.formDiv {
  background-color: #fff;
  min-width: 257px;
  border: 2px solid #888888;
  border-radius: 0.5%;
  font-size: 12.5px;
  padding: 15px;
}

.fsublock {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  padding: 2px;
}
.fsu {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  flex: 0 1 420px;
}
.fsuh4 {
  flex: 0 1 auto;
}
.fsublockmini {
  display: flex;
  flex-wrap: wrap;
  flex: 1 1 auto;
}
.fsublockoption {
  font-family: 'MuseoSansCyrl', 'serif';
  flex: 0 0 200px;
  border: px solid #d8d8d8;
  background-color: rgb(255, 255, 255);
}
.infoblock {
  display: flex;
  flex-wrap: wrap;
  min-width: 257px;
  border-radius: 5px;
  padding-left: 1%;
  min-height: 55px;
  justify-content: flex-start;
  align-items: flex-end;
  margin-right: 20px;
  box-shadow: 0px 0px 2px 0px #383838;
  background-color: #f0f0f0;
  margin-bottom: 10px;
}
.infoblockDiv {
  width: 200px;
  height: 22px;
  display: flex;
  align-items: center;
  border: 0px solid #888888;
  box-shadow: 0px 0px 1px 1px rgb(68, 68, 68);
}

.infoblockQuantity {
  display: flex;
  flex-direction: column;
  min-width: 257px;
  border: 3px solid #888888;
  border-radius: 10px;
  padding-left: 1%;
  min-height: 55px;
  justify-content: flex-end;
  align-items: flex-start;
}

.infoblockQuantityDetail {
  display: flex;
  flex-wrap: wrap;
  max-width: 175vh;
  min-height: 55px;
  justify-content: flex-start;
  align-items: flex-end;
}
.infoblockGroup {
  display: flex;
  border-radius: 50px;
  background-color: #cecece;
  flex-wrap: wrap;
  padding: 5px;
  border-radius: 10px;
  padding-left: 1%;
  min-height: 55px;
  margin-right: 20px;
  margin-bottom: 10px;
  box-shadow: 0px 0px 2px 0px #3e3e3e;
}
.awb {
  padding-left: 1.5%;
}
.detailAndTotal {
  border: 2px solid;
  border-radius: 0.5%;
  width: 175vh;
  padding-left: 1%;
  margin-left: 2vh;
  height: 14vh;
}
.detail {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(7, 1fr);
}
.total {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(7, 1fr);
}
h4 {
  margin: 0;
  padding: 0.5%;
  font-size: 15px;
}
.MovementDetail {
  border: 2px solid;
  border-radius: 0.5%;
  width: 175vh;
  padding-left: 1%;
  margin-left: 2vh;
  height: 27.5vh;
}
.movement {
  display: grid;
  align-items: center;

  grid-template-columns: repeat(7, 1fr);
}
.time {
  display: grid;
  align-items: center;

  grid-template-columns: repeat(7, 1fr);
}

.validate {
  overflow: auto;
  background-color: red;
  border: 2px solid;
  border-radius: 0.5%;
  min-width: 300px;
  margin-top: 20px;
  max-width: 300px;
  margin-bottom: 20px;
  padding-left: 1%;
  height: 9.5vh;
  color: gray;
  white-space: pre-line;
}

.validateMessage {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.message {
  margin-left: 0.25%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 30px;
  font-weight: bold;
  font-size: 15px;
  padding: 10px 25px;
  margin-bottom: 20px;
  cursor: pointer;
  text-decoration: none;
  outline: none;
  color: rgb(30, 30, 30);
  background-color: #6c98ff;
  border: none;
  border-radius: 5px;
  box-shadow: 0px 0px 3px 1px #aeaeae;
}

.message:hover {
  box-shadow: 0px 0px 5px 1px #4a4a4a;
  background-color: #1d49aa;
}
.message:focus {
  outline: none;
  box-shadow: 0 0 0 4px #cbd6ee;
}

.publish {
  display: flex;
  justify-content: flex-end;
}

.errorValidation {
  bottom: 0px;
  color: rgb(255, 0, 0);
  font-size: 10px;
  line-height: 10px;
  padding: 2px 0px;
  margin: 0;
}
#received-from-block {
  min-width: 400px;
}
#received-from-input {
  font-size: 10px;
  width: 400px;
}
.btnBlock {
  height: 40px;
  padding: 0 25px;
  flex: 1 1 auto;
  flex-wrap: wrap;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.btnDelBlock {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.25%;
  width: 120px;
  min-height: 20px;
  font-weight: bold;
  font-size: 15px;
  cursor: pointer;
  text-decoration: none;
  outline: none;
  color: rgb(39, 39, 39);
  border: none;
  border-radius: 5px;
  box-shadow: 0px 0px 2px 0px #000000;
  background-color: #ffc062;
}
.btnDelBlock:disabled {
  cursor: default;
  background-color: rgb(194, 194, 194);
  color: #caa7a7;
}
.btnAddBlock {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.25%;
  width: 120px;
  min-height: 20px;
  font-weight: bold;
  font-size: 15px;
  cursor: pointer;
  text-decoration: none;
  outline: none;
  color: rgb(39, 39, 39);
  border: none;
  border-radius: 5px;
  box-shadow: 0px 0px 2px 0px #000000;
  background-color: #8e9b80;
}
.btnAddBlock:disabled {
  cursor: default;
  background-color: rgb(194, 194, 194);
  color: #888888;
}
.btnBlockElement {
  width: 100%;
  height: 40px;
  padding: 0 25px;
  flex: 1 1 auto;
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
  align-items: center;
}
.groupTitle {
  line-height: normal;
  padding: 0;
  margin: 0;
  height: 15px;
  display: flex;
  flex: 1 1 100%;
}

