}
.sendingInv {
  display: flex;
  align-items: center;
  background-color: rgb(221, 221, 221);
  border-radius: 5px;
  padding: 5px;
  cursor: pointer;
}
.sendingInvDiv {
  display: flex;
  justify-content: center;
