.validateMessage {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.validate {
  overflow: auto;
  border: 2px solid;
  border-radius: 0.5%;
  min-width: 300px;
  max-width: 300px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-left: 1%;
  height: 9.5vh;
  color: gray;
  white-space: pre-line;
}
.message {
  margin-left: 0.25%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 30px;
  font-weight: bold;
  font-size: 15px;
  padding: 10px 25px;
  margin-bottom: 20px;
  cursor: pointer;
  text-decoration: none;
  outline: none;
  color: rgb(30, 30, 30);
  background-color: #d3cf59;
  border: none;
  border-radius: 5px;
  box-shadow: 0px 0px 3px 1px #aeaeae;
}
.message:hover {
  box-shadow: 0px 0px 5px 1px #4a4a4a;
  background-color: #f5ea4f;
}
.message:focus {
  outline: none;
  box-shadow: 0 0 0 4px #cbd6ee;
}
