.modal-div{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.modal-btn{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left:0.25%;
  width: 200px;
  height: 30px;
  font-weight: bold;
  font-size: 15px;
  padding: 10px 25px;
  margin-bottom: 20px;
  cursor: pointer;
  text-decoration: none;
  outline: none;
  color: black;
  background-color: #dadada;
  border: none;
  border-radius: 5px;
  box-shadow: 0 4px #aeaeae;
}
.modal-btn:hover{
  box-shadow: 0px 0px 5px 1px #4a4a4a;
  background-color: #464c51;
  color: rgb(255, 255, 255)
}
.modal-btn:focus{
  outline: none;
  box-shadow: 0 0 0 4px #cbd6ee;
}