.inputWrap input {
  padding: 0;
  margin: 0;
  text-transform: uppercase;
}
.inputWrap input[type='text'] {
  box-sizing: border-box;
}
.inputWrap input::placeholder {
  text-transform: none;
}
.inputWrap {
  line-height: 15px;
  width: 200px;
  margin-right: 20px;
  margin-bottom: 5px;
}
.inputWrap label {
  font-weight: bold;
}
.inputWrap select {
  padding: 0px;
  margin: 0px;
}
.inputWrapReceived {
  margin-right: 20px;
  margin-bottom: 5px;
  width: 350px;
}

.inputWrapReceived input {
  padding: 0;
  margin: 0;
  text-transform: uppercase;
  width: 350px;
}
.inputWrapReceived label {
  font-weight: bold;
}
.inputWrapReceived input::placeholder {
  text-transform: none;
}

.formDiv {
  background-color: #fff;
  min-width: 257px;
  border: 2px solid #888888;
  border-radius: 0.5%;
  font-size: 12.5px;
  padding: 15px;
}

.fsublock {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  padding: 2px;
}
.fsu {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  flex: 0 1 420px;
}
.fsuh4 {
  flex: 0 1 auto;
}
.fsublockmini {
  display: flex;
  flex-wrap: wrap;
  flex: 1 1 auto;
}
.fsublockoption {
  font-family: 'MuseoSansCyrl', 'serif';
  flex: 0 0 200px;
  border: 3px solid #888888;
  background-color: rgb(255, 255, 255);
}
.infoblock {
  display: flex;
  flex-wrap: wrap;
  min-width: 257px;
  border: 3px solid #888888;
  border-radius: 0.5%;
  padding-left: 1%;
  min-height: 55px;
  justify-content: flex-start;
  align-items: flex-end;
  margin-right: 20px;
}
.infoblockDiv {
  width: 200px;
  height: 22px;
  display: flex;
  align-items: center;
  /* justify-content: center; */
  /* flex-direction: column; */
  border: 0px solid #888888;
  box-shadow: 0px 0px 1px 1px rgb(68, 68, 68);
}

.infoblockQuantity {
  display: flex;
  flex-direction: column;
  min-width: 257px;
  border: 3px solid #888888;
  border-radius: 0.5%;
  padding-left: 1%;
  min-height: 55px;
  justify-content: flex-end;
  align-items: flex-start;
  margin-right: 20px;
}

.infoblockQuantityDetail {
  display: flex;
  flex-wrap: wrap;
  max-width: 175vh;
  min-height: 55px;
  justify-content: flex-start;
  align-items: flex-end;
}
.awb {
  padding-left: 1.5%;
}
.detailAndTotal {
  border: 2px solid;
  border-radius: 0.5%;
  width: 175vh;
  padding-left: 1%;
  margin-left: 2vh;
  height: 14vh;
}
.detail {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(7, 1fr);
}
.total {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(7, 1fr);
}
h4 {
  margin: 0;
  padding: 0.5%;
  font-size: 15px;
}
.MovementDetail {
  border: 2px solid;
  border-radius: 0.5%;
  width: 175vh;
  padding-left: 1%;
  margin-left: 2vh;
  height: 27.5vh;
}
.movement {
  display: grid;
  align-items: center;

  grid-template-columns: repeat(7, 1fr);
}
.time {
  display: grid;
  align-items: center;

  grid-template-columns: repeat(7, 1fr);
}

.message {
  margin-left: 0.25%;
  width: 200px;
  height: 30px;
  font-weight: bold;
  font-size: 15px;
  padding: 10px 25px;
  margin-bottom: 20px;
  cursor: pointer;
  text-decoration: none;
  outline: none;
  color: black;
  background-color: #dadada;
  border: none;
  border-radius: 5px;
  box-shadow: 0 4px #aeaeae;
}

.message:hover {
  box-shadow: 0px 0px 5px 1px #4a4a4a;
  background-color: #1d49aa;
}
.message:focus {
  outline: none;
  box-shadow: 0 0 0 4px #cbd6ee;
}

.publish {
  display: flex;
  justify-content: flex-end;
}

.errorValidation {
  bottom: 0px;
  color: rgb(255, 0, 0);
  font-size: 10px;
  line-height: 10px;
  padding: 2px 0px;
  margin: 0;
}
#received-from-block {
  min-width: 400px;
}
#received-from-input {
  font-size: 10px;
  width: 400px;
}
